body {
  --active-color: #00acdc;
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  padding-right: 243px;
  display: flex;
}

@media (width >= 1024px) {
  .header {
    padding: 0 50px;
  }
}

@media (width >= 1200px) {
  .header-logo {
    padding-right: 419px;
  }
}

.nav-list {
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style: none;
  display: flex;
}

.nav-item {
  text-transform: uppercase;
  align-items: center;
  display: flex;
}

.nav-item:not(:last-child) {
  margin-right: 20px;
}

.nav-link {
  color: #000;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
}

.nav-link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
